import axios from 'axios';

import common from './commonUtil';

const api = {
    // get
    get(url, data) {
        return axios({
            method: "get",
            url: common.isDev() ? `/static/json/${url}.json` : url,
            data: data ? data : {}
        });
    },

    // post
    post(url, data) {
        //get
        if (common.isDev()) {
            return this.get(url, data);
        }

        return axios({
            method: "post",
            url: url,
            data: data ? data : {}
        });
    }

}

export default api;



